import { CountUp } from "./countUp.min.js";

const countups = (goUp) =>
  [
    ["n1", 6],
    ["n2", 10],
    ["n3", 5],
    ["n4", 50],
  ].map(
    ([el, n]) =>
      new CountUp(el, goUp ? n : 0, {
        duration: 1,
        startVal: goUp ? 0 : n,
      })
  );

let goUp = false;
const observer = new IntersectionObserver(
  ([{ isIntersecting }]) => {
    console.log("coucou", isIntersecting);
    countups(isIntersecting).forEach((c) => c.start());
  },
  {
    threshold: 1,
  }
);

const initNumbers = () => {
  setTimeout(() => observer.observe(document.querySelector("#n1")), 2000);
};

addEventListener("load", () => {
  initNumbers();
});
